  background-color: #f8f8f8;
  color: #7859cf;
  font-weight: 600;
  text-align: end;
  width: 160px;
  padding-right: 0;
  padding-left: 0;
}

/* Style the arrow inside the select element: */
.select-selected::after {
  position: absolute;
  content: "";
  top: 17px;
  right: -5px;
  width: 10px;
  height: 6px;
  background-image: url("../img/tick.svg");
  background-size: auto;
